<template>
  <div class="content">
    <div class="info">
          <div class="text">
            <span class="tag">{{ details.tag }}</span><span class="title">{{ details.title }}</span>
          </div>
          <div class="time">{{ details.publish_time }}</div>
    </div>
    <img :src="details.thumb" :alt="details.title" />
  </div>
</template>

<script>
export default {
  data() {
    return {
      details: {
          title: "悦享美微商城今日正式上线啦~悦享美微商城今日正式上线啦~",
          article_id: 1,
          cat_id: 2,
          publish_time: "2019-07-08 12:20:56",
          tags: "3",
          thumb:require('@/assets/homepages/img5.png'),
          cat_name: "商城头条",
          tag: "精品"
        }
    }
  }
}
</script>

<style lang="scss" scoped>
  .content{
    .info{
      height: 180px;
      box-sizing: border-box;
      padding: 30px 30px 11px;
      border-bottom: 1px solid #E5E5E5;
      .text{
          height: 100px;
          color: #222;
          font-size: 30px;
          line-height: 40px;
          .tag{
            border: thin solid #FF4A54;
            color: #FF4A54;
            font-size:22px; 
            padding: 3px 7px;
            border-radius: 3px;
            margin-right:15px;
            
          }
           .title{
            line-height: 45px;
          }
      }
       .time{
          color: #888;
          font-size: 24px;
          margin-top: 6px;
          padding-left: 10px;
        }
    }
    img{
      width: 419px;
      height: 419px;
      margin-left: 30px;
      margin-top: 29px;
    }
  }
</style>



